<template>
  <div class="kfkform">
    <h1 class="main-title"> <span></span> <a href="javascript:void(0)" class="link" style="display:;" @click="goback">
    <span ></span>
    </a> </h1>
    <div class="beian-flow">
      <div class="beian-flow-guide">
        <div class="guide-box">
          <el-steps :space="100" direction="vertical" :active="step">
            <el-step title=""></el-step>
            <el-step title="loader"></el-step>
          </el-steps>
        </div>
        <div class="wiki-box">
          <p class="wiki-title"></p>
          <ul class="wiki-content">
            <li></li>
            <!--  <li>24243234</li>
            <li>23424</li>
            <li>342243</li> -->
          </ul>
        </div>
      </div>
      <div class="tc-panel">
        <div class="form-wrappe"> <span class="num">{{step}}</span>
          <h2 class="form-legend"> <span>{{step===1?'':'kafka loader'}}</span> </h2>
          <div v-show="step===1">
            <el-form :model="form" :rules="rules" ref="baseForm" label-position="left" style="padding-left:10px;">
              <el-form-item label="tserver" :label-width="formLabelWidth" prop="socketServerIp">
                <el-input v-model="form.socketServerIp" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="tserver" :label-width="formLabelWidth" prop="socketServerPort">
                <el-input v-model="form.socketServerPort" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="datahubVersion">
                <el-input v-model="form.datahubVersion" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="srcid" :label-width="formLabelWidth" prop="srcId">
                <el-input v-model="form.srcId" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="dip_xml_compress">
                <el-select v-model="form.dip_xml_compress" placeholder="" size="small">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="0"> </el-option>
                </el-select>
              </el-form-item>
              <!--  <el-input v-model="form.dip_xml_compress" auto-complete="off" size="small" style="width:300px;"></el-input>--></el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="dip_xml_group">
                <el-input v-model="form.dip_xml_group" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="dip_xml_queue_name">
                <el-input v-model="form.dip_xml_queue_name" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="loader_class_name">
                <el-input v-model="form.loader_class_name" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <div v-show="step===2">
            <el-form :model="form" :rules="rules" ref="loaderForm" label-position="left" style="padding-left:10px;">
              <el-form-item label="kafka topic" :label-width="formLabelWidth" prop="dip_kafka_topic">
                <el-input v-model="form.dip_kafka_topic" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="zookeeper" :label-width="formLabelWidth" prop="zk_hosts">
                <el-input v-model="form.zk_hosts" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="kafka borkers" :label-width="formLabelWidth" prop="kafka_brokers">
                <el-input v-model="form.kafka_brokers" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="testGroup" :label-width="formLabelWidth" prop="kafka_group_id">
                <el-input v-model="form.kafka_group_id" auto-complete="off" size="small" style="width:300px;"></el-input>
              </el-form-item>
              <el-form-item label="json" :label-width="formLabelWidth" prop="json_type">
                <el-select v-model="form.json_type" placeholder="" size="small">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="0"> </el-option>
                </el-select>
                <!--<el-input v-model="form.json_type" auto-complete="off" size="small" style="width:300px;"></el-input>--></el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="kafka_external_config">
                <el-input v-model="form.kafka_external_config" type="textarea" :autosize="{ minRows: 4}" placeholder=""
                  size="small" auto-complete="off" style="width:300px;"> </el-input>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="procedure">
          <div v-show="step===1">
            <el-button @click="goback"></el-button>
            <el-button type="primary" @click="baseConfig">loader</el-button>
          </div>
          <div v-show="step===2">
            <el-button @click="lastStep"></el-button>
            <el-button type="primary" @click="loaderConfig" :loading="loading"></el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {
    saveConfig
  } from '@/services/query';
  export default {
    data() {
      var validateServerIp = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('IP'));
        } else {
          let temp = value.replace(/(^\s*)|(\s*$)/g, '');
          if (!temp.length) {
            callback(new Error('IP!'));
          } else if (
            /^(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9])\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[0-9])$/
            .test(value) !== true) {
            callback(new Error('IP!'));
          } else {
            callback();
          }
        }
      };
      var validateport = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          let temp = value.replace(/(^\s*)|(\s*$)/g, '');
          if (!temp.length) {
            callback(new Error('!'));
          } else if (/^\d{2,5}$/.test(value) !== true) {
            callback(new Error('!'));
          } else {
            callback();
          }
        }
      };
      return {
        step: 1,
        loading: false,
        form: {
          socketServerIp: '',
          socketServerPort: '',
          datahubVersion: '',
          srcId: '',
          dip_xml_compress: '',
          dip_xml_group: '',
          dip_xml_queue_name: '',
          loader_class_name: '',
          dip_kafka_topic: '',
          zk_hosts: '',
          kafka_brokers: '',
          kafka_group_id: '',
          json_type: '',
          kafka_external_config: ''
        },
        formLabelWidth: '150px',
        rules: {
          socketServerIp: [{
            validator: validateServerIp,
            required: true,
            trigger: 'blur'
          }],
          socketServerPort: [{
            validator: validateport,
            required: true,
            trigger: 'blur'
          }],
          datahubVersion: [{
            required: true,
            trigger: 'blur',
            message: ''
          }, {
            min: 1,
            max: 10,
            message: ' 1  10 ',
            trigger: 'blur'
          }],
          srcId: [{
            required: true,
            trigger: 'blur',
            message: 'srcid'
          }, {
            min: 1,
            max: 40,
            message: ' 1  40 ',
            trigger: 'blur'
          }],
          dip_xml_compress: [{
            required: true,
            trigger: 'change'
          }],
          dip_xml_group: [{
            required: true,
            trigger: 'blur',
            message: ''
          }, {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }],
          dip_xml_queue_name: [{
            required: true,
            trigger: 'blur',
            message: ''
          }, {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }],
          loader_class_name: [{
            required: true,
            trigger: 'blur',
            message: ''
          }, {
            min: 1,
            max: 100,
            message: ' 1  100 ',
            trigger: 'blur'
          }],
          dip_kafka_topic: [{
            required: true,
            trigger: 'blur',
            message: 'kafka topic'
          }, {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }],
          zk_hosts: [{
            required: true,
            trigger: 'blur',
            message: 'zokeeper'
          }, {
            min: 1,
            max: 100,
            message: ' 1  100 ',
            trigger: 'blur'
          }],
          kafka_brokers: [{
            required: true,
            trigger: 'blur',
            message: 'kafkaborkers'
          }, {
            min: 1,
            max: 100,
            message: ' 1  100 ',
            trigger: 'blur'
          }],
          kafka_group_id: [{
            required: true,
            trigger: 'blur',
            message: 'groupid'
          }, {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }],
          json_type: [{
            required: true,
            trigger: 'change'
          }],
          kafka_external_config: [{
            required: true,
            trigger: 'blur',
            message: 'kafkajsonkey:value'
          }, {
            min: 1,
            max: 600,
            message: ' 1  600 ',
            trigger: 'blur'
          }]
        }
      }
    },
    components: {},
    created() {
      this.form = Object.assign({}, this.$store.state.common.kafkaList);
    },
    methods: {
      baseConfig() {
        this.$refs['baseForm'].validate((valid) => {
          if (valid) {
            this.step = 2;
          } else {
            return false;
          }
        });
      },
      lastStep() {
        this.step = 1;
      },
      loaderConfig() {
        this.loading = true;
        this.$refs['loaderForm'].validate((valid) => {
          if (valid) {
            saveConfig(this.form).then(res => {
              this.loading = false;
              if (res.success) {
                this.$alert('', '', {
                  confirmButtonText: '',
                  showClose: false,
                  type: 'success',
                  callback: () => {
                    this.$router.push({
                      path: '/main/kfk/list'
                    });
                  }
                })
              }
            })
          } else {
            return false;
          }
        });
      },
      goback() {
        this.$router.push({
          path: '/main/kfk/list'
        });
      }
    }
  }

</script>
<style lang="scss" scoped>
  .kfkform {
    width: calc(100% - 28px);
    padding: 14px;
    .main-title {
      font-size: 22px;
      color: #171d25;
      font-weight: 400;
      height: 61px;
      line-height: 61px;
      border-bottom: 1px solid #bec0c3;
      margin: 0 20px 16px;
      .link {
        font-size: 14px;
        float: right;
        text-decoration: none;
        color: #2277da;
      }
    }
    .beian-flow {
      width: 100%;
      position: relative;
      display: table;
      .beian-flow-guide {
        display: table-cell;
        width: 220px;
        border-right: 1px solid #edeef2;
        vertical-align: top;
        .guide-box,
        .wiki-box {
          margin: 0 20px;
          padding-bottom: 20px;
          border-bottom: 1px solid #edeef2;
          .wiki-title {
            font-size: 14px;
            color: #333;
            margin-bottom: 10px;
          }
          .wiki-content {
            font-size: 12px;
            color: #2277da;
            line-height: 24px;
          }
        }
      }
      .tc-panel {
        margin-right: auto;
        box-shadow: none;
        padding: 0;
        max-width: inherit;
        margin-left: 0;
        box-sizing: border-box;
        .form-wrappe {
          position: relative;
          border: 1px solid #dce0e8;
          padding: 20px;
          margin: 20px 30px;
          .num {
            display: block;
            width: 28px;
            height: 28px;
            background-image: url(../../assets/img/beian-201708091817.png);
            position: absolute;
            top: 0;
            left: 0;
            line-height: 19px;
            color: #fff;
            font-size: 12px;
            text-indent: 4px;
            background-position: -283px -64px;
          }
          .form-legend {
            font-size: 14px;
            color: #171d25;
            padding-left: 10px;
            line-height: 16px;
          }
        }
        .procedure {
          margin: -1px 20px 100px;
          margin-left: 30px;
          padding-top: 20px;
          border-top: solid 1px #e2e2e3;
        }
      }
    }
  }

</style>



