<template>
  <div class="kfkloader">
    <jd-card :newButton="true" @handleClick="handlePop('create')" btnname="kfk">
      <div slot="title">KFK</div>
      <div slot="body">
        <el-table :data="tableData" stripe>
          <el-table-column prop="socketServerIp" label="tserver" show-overflow-tooltip> </el-table-column>
          <el-table-column prop="socketServerPort" label="tserver" show-overflow-tooltip> </el-table-column>
          <!--  <el-table-column prop="datahubVersion" label=""> </el-table-column>
          <el-table-column prop="srcId" label="" > </el-table-column>
          <el-table-column prop="dip_xml_compress" label=""> </el-table-column>
          <el-table-column prop="dip_xml_group" label="" > </el-table-column>
          <el-table-column prop="dip_xml_queue_name" label=""> </el-table-column>-->
          <el-table-column prop="loader_class_name" label="" show-overflow-tooltip> </el-table-column>
          <el-table-column prop="dip_kafka_topic" label="kafka topic" show-overflow-tooltip> </el-table-column>
          <el-table-column prop="zk_hosts" label="zookeeper" show-overflow-tooltip> </el-table-column>
          <el-table-column prop="kafka_brokers" label="brokers" show-overflow-tooltip> </el-table-column>
          <!--    <el-table-column prop="kafka_group_id" label="" > </el-table-column>
          <el-table-column prop="json_type" label="json"> </el-table-column>-->
          <el-table-column label="" width="200">
            <template scope="scope">
              <el-button type="text" @click="handlePop('lookup', scope.row)"></el-button>
              <el-button type="text" @click="handlePop('edit', scope.row)"></el-button>
              <el-button type="text" @click="handlePop('start', scope.row)"></el-button>
              <el-button type="text" @click="handlePop('stop', scope.row)"></el-button>
            </template>
          </el-table-column>
          </el-table-column>
        </el-table>
      </div>
    </jd-card>
    <div class="pagination">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[30, 50, 80, 100]"
        :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">dip tserver</div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.socketServerIp}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">dip tserver</div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.socketServerPort}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">dip  group</div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.dip_xml_group}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">dip etl </div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.dip_xml_queue_name}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">dip loader</div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.loader_class_name}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">kafka topic </div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.dip_kafka_topic}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">zookeeper </div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.zk_hosts}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">kafka  borkers </div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.kafka_brokers}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right"> groupId</div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.kafka_group_id}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">kafka json </div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.json_type}}</div>
        </el-col>
      </el-row>
      <el-row class="layout">
        <el-col :span="6">
          <div class="layout-right">kafka </div>
        </el-col>
        <el-col :span="18">
          <div>{{modaldata.kafka_external_config}}</div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>
<script>
  import jdCard from '@/components/common/card'
  import {
    getKafkaconfigList,
    startKfk,
    stopKfk
  } from '@/services/query'
  export default {
    data() {
      return {
        currentPage: 1,
        pageSize: 30,
        total: 0,
        tableData: [],
        dialogFormVisible: false,
        modaldata: {
          socketServerIp: '',
          socketServerPort: '',
          datahubVersion: '',
          srcId: '',
          dip_xml_compress: '',
          dip_xml_group: '',
          dip_xml_queue_name: '',
          loader_class_name: '',
          dip_kafka_topic: '',
          zk_hosts: '',
          kafka_brokers: '',
          kafka_group_id: '',
          json_type: '',
          kafka_external_config: ''
        }
      }
    },
    components: {
      jdCard,
      // jdModal
    },
    created() {
      // this.$store.commit('USER_INFO', res.data);
      console.log(this.$store.state.common.kafkaList)
      getKafkaconfigList({
        page: this.currentPage,
        size: this.pageSize
      }).then(res => {
        if (res.success) {
          this.tableData = res.data;
          // this.$store.commit('KAFKA', res.data);
          this.total = res.data.length;
        }
      })
    },
    methods: {
      newClick() {
        this.$router.push({
          path: '/main/kfk/config'
        });
      },
      handlePop(state, row) {
        if (state === 'create') {
          this.$store.commit('KAFKA', {
            socketServerIp: '',
            socketServerPort: '',
            datahubVersion: '',
            srcId: '',
            dip_xml_compress: '',
            dip_xml_group: '',
            dip_xml_queue_name: '',
            loader_class_name: '',
            dip_kafka_topic: '',
            zk_hosts: '',
            kafka_brokers: '',
            kafka_group_id: '',
            json_type: '',
            kafka_external_config: ''
          });
          this.$router.push({
            path: '/main/kfk/config'
          });
        } else if (state === 'edit') {
          this.$store.commit('KAFKA', row);
          this.$router.push({
            path: '/main/kfk/config'
          });
        } else if (state === 'lookup') {
          this.modaldata = row;
          this.dialogFormVisible = true;
        } else if (state === 'start') {
          this.$msgbox({
            title: '',
            message: '',
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            beforeClose: (action, instance, done) => {
              if (action === 'confirm') {
                instance.confirmButtonLoading = true;
                instance.confirmButtonText = '...';
                startKfk().then(res => {
                  if (res.success) {
                    instance.confirmButtonLoading = false;
                    done();
                  }
                });
              }
            }
          });
        } else if (state === 'stop') {
          this.$msgbox({
            title: '',
            message: '',
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            beforeClose: (action, instance, done) => {
              if (action === 'confirm') {
                instance.confirmButtonLoading = true;
                instance.confirmButtonText = '...';
                stopKfk().then(res => {
                  if (res.success) {
                    instance.confirmButtonLoading = false;
                    done();
                  }
                });
              }
            }
          });
        }
      },
      handleConfirm() {},
      handleSizeChange(val) {},
      handleCurrentChange(val) {}
    }
  }

</script>
<style lang="scss" scoped>
  .kfkloader {
    width: calc(100% - 28px);
    height: calc(100% - 28px);
    padding: 14px;
    .el-table {
      height: calc(100vh - 182px);
      border: 0px;
      &::after {
        width: 0;
      }
      &::before {
        height: 0;
      }
    }
    .pagination {
      text-align: center;
      margin-top: 14px;
    }
    .layout {
      margin-bottom: 10px;
      .layout-right {
        float: right;
        color: black;
      }
    }
  }

</style>
<style lang="scss">
  .kfkloader {
    .el-table__body-wrapper {
      height: calc(100% - 30px);
    }
  }

</style>



